import styled from "styled-components";
import { BoxContentCenter } from "../Box";

export const Button = styled(BoxContentCenter).attrs({
  as: "button",
})`
  position: relative;
  z-index: 1;
  overflow: hidden;

  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(${(props) => props.theme.color.ultramarineBlue}, 1);

  transition: background-color 150ms ease-in;
  cursor: pointer;

  /* &:hover {
    background-color: rgba(
      ${(props) => props.theme.color.ultramarineBlue},
      0.5
    );
  } */

  /* &:active { */
  /* background-color: rgba(${(props) =>
    props.theme.color.ultramarineBlue}, 1); */
  /* } */

  &:active::after {
    transform: scale(1);
  }

  &::after {
    position: absolute;
    z-index: -1;

    width: 100%;
    height: 100%;
    background-color: rgba(
      ${(props) => props.theme.color.ultramarineBlue},
      0.5
    );

    transition: transform 150ms ease-in;
    transform: scale(1);
    content: "";
  }
`;
